import React from 'react';
import './statbox.css';

const StatsBox = ({ dollarIncome, hoursPassed, daysPassed, yearsPassed, mainHappiness, happinessIncome, spending, annualBonus}) => {


    return (
        <div id='statbox' className='statbox light-gray'>
            <p className='statincome light-green'>Your Current Income is ${dollarIncome} / hour</p>
            <p className=' statincome light-green'>Your Annual Bonus is ${annualBonus}</p>
            <p className='stathappy'>Total &#x1F604;: {mainHappiness}</p> 
            <p className='stathappy'>&#x1F604; per day: {happinessIncome}</p> 
            <p className='costliving'>Cost of living: ${spending} / day</p>

            <p className='light-yellow'>{Math.round(yearsPassed)} years / {Math.round(daysPassed)} days / {hoursPassed} hours passed</p>


        </div>
    );
}

export default StatsBox;